import UserModel from '@/models/user.js'
import PostModel from '@/models/job.js'
import ListItem from '@/components/ListItem/ListItem.vue'
const userModel = new UserModel() 
const postModel = new PostModel()
export default {
	data(){
		return {
			background: ['color1', 'color2', 'color3'],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			postList: [],
			pageNum: 1,
			pageSize: 10,
			keyword: '',
			label:'',
			userType: 0
		}
	}, 
	components:{
		ListItem
	},
	mounted() {
		this.userType = uni.getStorageSync('userType') || 0
		this.getJobList()
	},
	methods:{
		chooseImage(){
			uni.chooseImage({
				success(res) {
					const tempFilePath = res.tempFilePaths
					uni.uploadFile({
						url:'http://baidu.com',
						filePath:tempFilePath[0],
						name:'file',
						formData:{
							
						},
						success(uploadFileRes){
							console.log(uploadFileRes.data)
						}
					})
				}
			})
		},
		getUserInfo(e){
			console.log(e)
		},
		getJobList(){
			postModel.jobList(this.pageNum, this.pageSize, this.keyword, this.label).then(res=>{
				const { code, message, data } = res.data
				if(code === '0'){
					this.postList = data
				}else{
					uni.showToast({
						icon: 'none',
						title: message
					})
				}
			}).catch(err=>{
				uni.showToast({
					icon: 'none',
					title:'获取岗位列表信息失败'
				})
			})
		},
		showDetail(id){
			const that = this
			uni.checkSession({
				success(res) {
					const token = uni.getStorageSync('token')
					const isLogin = uni.getStorageSync('isLogin')
					const isRegister = uni.getStorageSync('isRegister')
					if(token && isLogin === 0 && isRegister === 0){
						uni.navigateTo({
							url: '../../public/PostDetail/PostDetail?id='+id
						})
					}else if(token && isLogin === 0){
						uni.navigateTo({
							url: '../../user/inputInfo/inputInfo'
						})
					}else{
						that.selectLoginType()
					}
				},
				fail(err) {
					that.selectLoginType()
				}
			})
		},
		login(){
			uni.login({
				provider: 'weixin',
				success(res){
					if(res.errMsg === 'login:ok'){
						// 执行后台登录
						userModel.wxLogin({
							code: res.code
						}).then(res=>{
							if(res.code === 'success'){
								// 将返回的token存入本地
								uni.setStorageSync('token', res.data.token)
							}else{
								uni.showToast({
									icon: 'none',
									title: res.msg
								})
							}
						})
					}else{
						uni.showToast({
							icon: 'none',
							title: res.errMeg
						})
					}
				},
				fail(err) {
					console.log(err)
				}
			})
		},
		selectLoginType(){
			this.$emit('showLogin')
		},
		switchToSeller(){
			uni.reLaunch({
				url: '../../seller/main/main'
			})
		}
	}
}
